
import XHeader from 'vux/src/components/x-header/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

export default {
name: 'PatientManage',
components:{
XHeader,Swipeout, SwipeoutItem, SwipeoutButton
},
mounted(){
this.qryPatients();
},
data () {
return {
noData: false,
list: [],
}
},
methods: {
qryPatients(){  // 获取就诊人列表
this.$vux.loading.show({
text: '加载中...'
})
this.$ajax.post('',{
"MSH.1": "visitor",
"MSH.2": "visiterList",
token:this.$root.loginInfo.token, //token
pageIndex:1,
pageSize:100

}).then(res => {
this.$vux.loading.hide();
if(typeof res != 'undefined' && typeof res.rows != 'undefined') {
this.list = [];
if (res.rows.length > 0) {
this.list = this.list.concat(res.rows);
this.noData = false;
} else {
this.noData = true;
}
} else {
this.noData = true;
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
this.noData = true;
})
},
deletePatientReq(obj) {
let THIS = this;
this.$ajax.post('',{
"MSH.1": "visitor",
"MSH.2": "delVisiter",
token:this.$root.loginInfo.token, //token
idcardno:obj.CZJHM,
idcardTypeBm:obj.CZJLX,
}).then(res => {
if(typeof res != 'undefined' && typeof res.status != 'undefined' && res.status == 1) {
this.$vux.toast.show({
type:'success',
time: '1000',
text: '删除成功!',
onHide(){
THIS.qryPatients();
}
})
}else{
this.$vux.toast.show({
type:'cancel',
time: '1000',
text: '删除失败!',
onHide(){
}
})
}
}).catch(error => {
console.log(error);
})
},
delPatients(obj){  // 删除我的就诊人
let THIS = this;
this.$vux.confirm.show({
title:'操作',
content:'确定删除就诊人吗?',
onConfirm () {
THIS.deletePatientReq(obj);
}
})
},
gotoCardList(info) {
this.$router.push({name:"visitCard", params:{jzrInfo:info}});
},
addClass(temp) {
if (temp == "自己") {
return "self";
} else if (temp == "朋友") {
return "friend";
} else if (temp == "家人") {
return "family";
}
},
}
}



